.btn {
  height: 44px;
  font-size: 14px;
  outline: none;
  line-height: 42px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #3594F5;
  border: 0 none;
  display: block;
  width: 100%;
}
.btn-min {
  display: inline-block;
  height: 22px;
  line-height: 20px;
  width: auto;
}
.btn-small {
  display: inline-block;
  height: 32px;
  line-height: 30px;
  width: auto;
}
.btn-large {
  height: 50px;
  line-height: 48px;
}
.btn-success {
  color: #fff;
  background-color: #6FD700;
}
.btn-warn {
  color: #fff;
  background-color: #F6C900;
}
.btn-danger {
  color: #fff;
  background-color: #F75500;
}
.btn-empty {
  color: #666;
  background-color: #fff;
  border: 1px solid #e1e1e1;
}
.btn.active {
  display: inline-block;
  animation: rotates 1.2s linear;
  animation-iteration-count: infinite;
  width: 14px;
  height: 14px;
}
.btn.active.iconfont {
    margin-right: 5px;
}
@keyframes rotates {
0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
