.toast[data-v-52c19bfc] {
  padding: 0 10px;
  max-width: 80%;
  max-height: 60px;
  line-height: 30px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 4px;
  text-align: center;
  position: fixed;
  color: #fff;
  font-size: 12px;
  box-sizing: border-box;
  z-index: 99999;
  transition: all ease .3s;
}
.top[data-v-52c19bfc] {
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}
.middle[data-v-52c19bfc] {
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}
.bottom[data-v-52c19bfc] {
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
}

/*vue2.0 transition*/
.toast-enter[data-v-52c19bfc] {
  transform: translateX(-50%);
  opacity: 0.7;
}
.toast-enter-active[data-v-52c19bfc] {
  transition: all ease-in-out .6s;
  transform: translateX(-50%) scale(0.8);
  opacity: 0;
}
.toast-leave-active[data-v-52c19bfc] {
  transition: all ease-in-out 1s;
  transform: translateX(-50%);
  opacity: 0.7;
}
.toast-leave-to[data-v-52c19bfc] {
  opacity: 0;
  transform: translateX(-50%) scale(0.8);
}



